<template>
    <div class="products__wrapper">
        <div class="products">
            <div class="products__item" v-for="product in products">
                <p class="products__item__title">
                    {{product.title}}
                </p>
                <div class="products__item__photo">
                    <img class="products__item__photo__icon" :src="product.images[0]" alt="">
                    {{product}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    computed: {
        products() {
            let products = this.$store.getters.products;

            function findCat(items, cat) {
                for(let key in items) {
                    if(key == cat) {
                        return items[key]
                    }

                    if(!Array.isArray(items[key])) {
                        return findCat(items[key], cat)
                    }
                }
            }

            let currProd = findCat(products, this.$route.params.product);
            return currProd
        }
    }
}
</script>

<style lang="scss" scoped>
    .products__wrapper {
        padding: 0 20px;
    }
</style>